import React, { Component } from 'react';

import Button from '../button/button.js';
import quotes from '../data/quotes';
import colors from '../data/colors';
import Spinner from '../spinner/spinner';

import './app.scss'
import '@fortawesome/fontawesome-free/css/all.css';

export default class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      color: 1,
      content: null,
      author: null
    }
    this.colorsNumber = colors.length;
    this.getQuote()
  }


  getQuote = async () => {
    const result = await fetch('https://api.quotable.io/random');
    const bodyInInterval= await result.json()
      .then(object => {
        this.setState({
          content: null,
          author: null,
          color:Math.floor(Math.random()*this.colorsNumber)
        })
        return object;
      })
      .then(object=>{
        setTimeout(() => {
          this.setState({
            content:object.content,
            author:object.author
          })
        }, 1000);
      })
  }

  render() {
    if(!this.state.content){
      return(
        <div className="app-wrapper">
          <div className="quote-box" id="quote-box">
            <Spinner/>
          </div>
        </div>
      )
    }
    return (
      <div className="app-wrapper"
        style={{
          backgroundColor: colors[this.state.color],
          color: colors[this.state.color]
        }}>
        <div className="quote-box" id="quote-box">
          <div className="text">
            <span className="text__icon">
              <i className="fas fa-quote-right"></i>
            </span>
            <span className="text__content">{this.state.content}</span>
          </div>
          <div className="author">{this.state.author}</div>
          <Button background={colors[this.state.color]} onClick={this.getQuote} />
        </div>
      </div>
    )
  }
}
